"use client"

import type React from "react"
import { createContext, useContext, useEffect, useState } from "react"
import {
  type User,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signOut as firebaseSignOut,
  createUserWithEmailAndPassword,
} from "firebase/auth"
import { doc, getDoc, setDoc } from "firebase/firestore"
import { auth, db } from "@/lib/firebase"

export interface Usuario {
  uid: string
  email: string | null
  nombre?: string
  rol: "admin" | "vendedor" | "trabajador"
  tipo?: string
  estado?: string
  fechaCreacion?: Date
}

interface AuthContextType {
  user: Usuario | null
  loading: boolean
  signIn: (email: string, password: string) => Promise<void>
  signOut: () => Promise<void>
  signUp: (email: string, password: string, userData?: Partial<Usuario>) => Promise<void>
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<Usuario | null>(null)
  const [loading, setLoading] = useState(true)

  // Fetch user data from Firestore
  const fetchUserData = async (firebaseUser: User): Promise<Usuario | null> => {
    try {
      console.log("[v0] üîç Fetching user data for UID:", firebaseUser.uid)
      console.log("[v0] üîç Email:", firebaseUser.email)

      const userDocRef = doc(db, "usuarios", firebaseUser.uid)
      console.log("[v0] üîç Firestore path:", `usuarios/${firebaseUser.uid}`)

      const userDoc = await getDoc(userDocRef)

      if (userDoc.exists()) {
        const userData = userDoc.data()

        console.log("[v0] ‚úÖ Documento encontrado en Firestore:")
        console.log("[v0] üìÑ Raw data:", JSON.stringify(userData, null, 2))
        console.log("[v0] üë§ Nombre:", userData.nombre)
        console.log("[v0] üé≠ Rol:", userData.rol)
        console.log("[v0] üìß Email:", userData.email)
        console.log("[v0] üîë UID del documento:", userDoc.id)

        return {
          uid: firebaseUser.uid,
          email: firebaseUser.email,
          nombre: userData.nombre || "Usuario",
          rol: userData.rol || "trabajador",
          tipo: userData.tipo,
          estado: userData.estado,
          fechaCreacion: userData.fechaCreacion?.toDate(),
        }
      } else {
        console.error("[v0] ‚ùå NO se encontr√≥ documento en Firestore para UID:", firebaseUser.uid)
        console.error("[v0] ‚ùå Verifica que el usuario exista en la colecci√≥n 'usuarios'")

        // Return basic user info if no Firestore document exists
        return {
          uid: firebaseUser.uid,
          email: firebaseUser.email,
          rol: "trabajador",
        }
      }
    } catch (error) {
      console.error("[v0] ‚ùå Error al obtener datos del usuario:", error)
      return null
    }
  }

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
      if (firebaseUser) {
        const userData = await fetchUserData(firebaseUser)
        setUser(userData)

        console.log("[v0] üéØ Estado final del usuario:")
        console.log("[v0] üìß Email:", userData?.email)
        console.log("[v0] üé≠ Rol:", userData?.rol)
        console.log("[v0] üë§ Nombre:", userData?.nombre)
      } else {
        setUser(null)
        console.log("[v0] ‚ö†Ô∏è No hay usuario autenticado")
      }
      setLoading(false)
    })

    return () => unsubscribe()
  }, [])

  const signIn = async (email: string, password: string) => {
    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password)
      const userData = await fetchUserData(userCredential.user)
      setUser(userData)
    } catch (error) {
      console.error("[v0] Error al iniciar sesi√≥n:", error)
      throw error
    }
  }

  const signOut = async () => {
    try {
      await firebaseSignOut(auth)
      setUser(null)
    } catch (error) {
      console.error("[v0] Error al cerrar sesi√≥n:", error)
      throw error
    }
  }

  const signUp = async (email: string, password: string, userData?: Partial<Usuario>) => {
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, password)

      // Create user document in Firestore
      await setDoc(doc(db, "usuarios", userCredential.user.uid), {
        email,
        nombre: userData?.nombre || email.split("@")[0],
        rol: userData?.rol || "trabajador",
        estado: "habilitado",
        fechaCreacion: new Date(),
        ...userData,
      })

      const newUserData = await fetchUserData(userCredential.user)
      setUser(newUserData)
    } catch (error) {
      console.error("[v0] Error al registrar usuario:", error)
      throw error
    }
  }

  const value = {
    user,
    loading,
    signIn,
    signOut,
    signUp,
  }

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth debe ser usado dentro de un AuthProvider")
  }
  return context
}
